        } 
        }
  
 /* BUTTON SEE PROJECT    */
.btn {
  font-size: 1.5rem;
  margin-top: 2rem;
  border: none;
  font-weight: 500;
  text-transform: uppercase;
  border-radius: 60px;
  background-image: linear-gradient(to right, #b44c06, #a05c0e9f);
  background-image: linear-gradient(to right, var(--color-main-dark), var(--color-main-dark-2));
  color: #fff;
  position: relative;
  overflow: hidden;
  cursor: pointer; 
  z-index: 10;
}
  @media only screen and (max-width: 37.5em) {
    .btn {
      margin-bottom: 1em; }
     }
  @media only screen and (max-width: 56.25em) {
    .btn {
      margin-bottom: 0; } 
    }
  .btn__visible {

    display: inline-block;
    padding: 2rem 3rem;
    height: 100%;
    width: 100%;
    transform: translateX(-3rem);
    transition: all .3s; 
  }
    @media only screen and (max-width: 56.25em) {
      .btn__visible {
        padding: 2rem 3.5rem; }
       }
  .btn__invisible {
    
    display: inline-block;
    position: absolute;
    padding: 2rem 0;
    height: 100%;
    width: 100%;
    left: 0;
    top: -100%;
    transition: all .3s;
   }
  .btn:hover {
    background-image: linear-gradient(to left, #c56d20, #a05c0e9f);
    background-image: linear-gradient(to left, var(--color-main-light), var(--color-main-dark-2)); }
  .btn:hover .btn__visible {
    transform: translateY(100%); }
  .btn:hover .btn__invisible {
    top: 0; 
  }
  .btn:focus {
    outline: none;
    -webkit-animation: btn-inline 2s infinite;
            animation: btn-inline 2s infinite; 
  }

  .btn a{
    text-decoration: none ;
    color:white;
  }
