@import url('https://fonts.googleapis.com/css?family=Open+Sans');


:root {
    --color-main-light: #c56d20;
    --color-main-transparent: #f5bb88;
    --color-main-dark: #b44c06;
    --color-main-dark-2: #a05c0e9f;

    --color-grey-light:#eee;
    --color-grey-light-2:#f4f2f2;
    --color-grey-medium:#808080;
    --color-grey-medium-2:#c9c7c7;
    --color-grey-medium-3:#cecece;
    --color-grey-dark:#333;
    --shadow-dark:0 2rem 6rem rgba(5,3,0,.3);
    --shadow-light:0 2rem 5rem rgba(0,0,0,.06);
    --shadow-light-2:0 2rem 5rem rgba(0,0,0,.3);

    --line: 2px solid var(--color-grey-medium-2); 

  }
  
html {
  box-sizing: border-box;
  font-size: 62.5%; 
}
  @media screen and (max-width: 68.75em) {
    html {
      font-size: 50%; } }

  *{
      margin: 0;
      padding: 0;
    }

    .container{
      font-family: 'Open Sans', sans-serif;
      box-sizing: border-box;
      font-weight: 400;
      line-height: 1.6;
      background-image: linear-gradient(to bottom,var(--color-main-light),var(--color-main-dark));
      background-size: cover;
      background-repeat: no-repeat;
      min-height: 100vh; 
    }

  .container {
    max-width: 120rem;
    margin: 8rem auto;
    background-color: var(--color-grey-light-2);
    box-shadow: var(--shadow-dark);
    min-height: 50rem;
    position: relative;
  }
    @media only screen and (max-width: 56.25em) {
      .container {
        margin: 0;
        max-width: 100%;
        width: 100%; } 
      }
        .logo{
          margin-left: 2rem;
          margin-top: 2rem;
          height: 6rem; 
          }

      /* HEADER-SECTION */
      .text-hi{
        font-size: 2.2rem;
        align-self: flex-start;
        margin-left:32rem;
        margin-top:6rem;
        margin-bottom:4rem;
      }
      @media only screen and (max-width: 56.25em) {
        .text-hi {
          margin-left:0;
        } 
        }   
      .img-hi{
        height: 2.3rem;
      }

      .header{
        font-size: 2.2rem;
        display: flex;
        flex-direction: column;
        align-items: center; 
        background-color: var(--color-grey-light);
        margin-bottom: 10rem;
        height: 50vh;
      }
      @media only screen and (max-width: 56.25em) {
         .header {
          height:fit-content;
          margin:0rem 2rem 5rem 2rem; 
          width:90%; 
        } 
        }

      .btn-info{
        align-self: flex-start;
        color:#ffffff;
        background-color: var(--color-main-dark);
        margin-left:32rem; 
        margin-top:4rem;
        padding: .5rem;
        font-size: 1.9rem;
        cursor: pointer;
        border: none;
      }
      .btn-info:hover{
        color:var(--color-main-light);
        background-color: #ebc785; 
      }
      @media only screen and (max-width: 56.25em) {
        .btn-info {
          margin-left:.5rem;
        } 
      }
      
      /* MODAL-INFO */
      
      .info-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(128, 128, 128, 0.699);
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.3s linear;
        z-index: 10;   
      }
      .info-container {
        background: #ffffff;
        border-radius: .5rem;
        font-size: 2.2rem;
        width: 90vw;
        height: 30vh;
        max-width: 62rem;
        text-align: center;
        display: flex;
        flex-direction: column;
        margin: auto;
        position: relative;
      }
      .close-btn {
        position: absolute;
        top: 1rem;
        right: 1rem;
        font-size: 2rem;
        background: transparent;
        border-color: transparent;
        color: var(--color-grey-medium);
        cursor: pointer;
        transition: all 0.3s linear;
      }
      .close-btn:hover {
        color: var(--color-main-dark);
        transform: scale(1.3);
      }

      /* SECTION QUALIFICATIONS-INFO */

      .qualifications-info{
        display: flex;
        align-items: center;
        justify-content: space-around;
        /*margin-bottom: 10rem;*/
        background-color: var(--color-grey-light-2);
      }
      .process,.about-me,.skills{
        height: 75vh;
        width: 23vw;
        font-size: 1.5rem;
        padding:2rem;
        background-color: var(--color-grey-light);
        color: var(--color-grey-dark);
        border-right: var(--line);
        border-bottom: var(--line);
        box-shadow: var(--shadow-light);
        margin-bottom: 5rem;
       }
       .process:hover,.about-me:hover,.skills:hover{
        box-shadow: var(--shadow-light-2);
        transform: translateY(1rem);
        cursor: pointer;
 
       }
      @media only screen and (max-width: 56.25em) {
        .qualifications-info{
        flex-direction: column;
        margin-bottom: 1rem;
        } 
        }

      @media only screen and (max-width: 56.25em) {
         .about-me, .skills,.process{
          height: fit-content;
          width: 95%;
          border-bottom: var(--line);
          border-right: none;
        } 
        }

        @media only screen and (max-width: 56.25rem){
          .ul{
            list-style-type: none;
          }
        }

        .btn-inline {
          border: none;
          color: var(--color-main-dark);
          font-size: 1.8rem;
          font-weight: 500;
          border-bottom: 1px solid currentColor;
          padding-bottom: 2px;
          display: inline-block;
          background-color: transparent;
          cursor: pointer;
          transition: all .2s; 
        }
        .btn-inline span {
          margin-left: 3px;
          transition: margin-left .2s; 
        }
        .btn-inline:hover {
          color: var(--color-grey-dark);
          background-color: var(--color-grey-light); }
          .btn-inline:hover span {
            margin-left: 8px; }
          .btn-inline:focus {
          outline: none;
          animation: btn-inline 1s infinite; }

          .btn-inline a{
            text-decoration: none;
            color: var(--color-main-dark);
          }
  

      @keyframes btn-inline {
        0% {
          transform: scale(1);
          box-shadow: none; }
        50% {
          transform: scale(1.05);
          box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25); }
        100% {
          transform: scale(1.05);
          box-shadow: none; } 
        }
      
      /* SECTION PROJECTS  */
      .projects{
        display: flex;
        flex-wrap: wrap;
      }
      @media only screen and (max-width: 56.25em) {
        .projects{
        flex-direction: column;
        } 
        }
      .react,.sass{
        height:50vh;
        background-size: cover;
        position: relative;
        text-align:center;
        padding: 3rem;
        width:50%;
      }

      .react{
      background-image: url('/img/react_hooks.png');
      background-position: left bottom;
      background-blend-mode: lighten;
      
      }
      .sass { 
        background-image: url('/img/responsive.jpeg') ;
        background-color: black;
      }
      
      .js{
        transform:translateX(30rem) translateY(-5rem);
        z-index: 3;
        box-shadow: var(--shadow-dark);
        margin-bottom: 5rem;
        height:40vh;
        background-size: cover;
        position: relative;
        text-align:center;
        padding: 3rem;
        width:50%;
        }
        @media only screen and (max-width: 56.25em) {
          .js{
          transform: none;
          z-index: inherit;
          box-shadow: none;
          margin-bottom: 3rem;
          border: 2px solid grey;
          } 
          }
         
      .sass p, .react p, .js p {
        position:relative;
        z-index:20;
      }

      .sass__paragraph , .react__paragraph{
        font-size: 2.2rem;
        font-weight: 300;
        text-align: center;
        color: #ffffff;
        
      }
      .js__paragraph{
        font-size: 2.2rem;
        font-weight: 800;
        text-align: center;
        color: black;
      }
            
      @media only screen and (max-width: 56.25em) {
        .react,.sass, .js{
         /* padding-top: 8rem; */
         background-position: cover;
         height: fit-content;
         margin-bottom: 3rem;
         width: auto;
        } 
        }
        
        .sass:before, .react:before{
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background-color: #000000b0;
          opacity: 1;
          z-index:0;
        }
        .js:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background-image: url('/img/javascript.jpg');
          background-position: center;
          background-size: 40%; 
          background-repeat: no-repeat;
          background-color: white;
          opacity: 1;
          z-index:0;
        }

.sass__paragraph:before, .react__paragraph:before, .js__paragraph:before{
    content: "";
    position: absolute;
}


      .project-title{
        text-align: center;
        font-size: 2.8rem;
        font-weight: 900;
        margin-bottom: 2rem;
      }
      
      /* FOOTER */
      .footer{
        text-align: right;
        color: #fff;
        padding:3rem;
        font-size: 2rem;
        background-color: var(--color-grey-dark);
        width: 100%;
        position: absolute;
        bottom: -60px;
        left: 0px
      }
      @media only screen and (max-width: 56.25em) {
        .footer {
          text-align: center;
          padding: 2rem;
        } 
        }
  
 /* BUTTON SEE PROJECT    */
.btn {
  font-size: 1.5rem;
  margin-top: 2rem;
  border: none;
  font-weight: 500;
  text-transform: uppercase;
  border-radius: 60px;
  background-image: linear-gradient(to right, var(--color-main-dark), var(--color-main-dark-2));
  color: #fff;
  position: relative;
  overflow: hidden;
  cursor: pointer; 
  z-index: 10;
}
  @media only screen and (max-width: 37.5em) {
    .btn {
      margin-bottom: 1em; }
     }
  @media only screen and (max-width: 56.25em) {
    .btn {
      margin-bottom: 0; } 
    }
  .btn__visible {

    display: inline-block;
    padding: 2rem 3rem;
    height: 100%;
    width: 100%;
    transform: translateX(-3rem);
    transition: all .3s; 
  }
    @media only screen and (max-width: 56.25em) {
      .btn__visible {
        padding: 2rem 3.5rem; }
       }
  .btn__invisible {
    
    display: inline-block;
    position: absolute;
    padding: 2rem 0;
    height: 100%;
    width: 100%;
    left: 0;
    top: -100%;
    transition: all .3s;
   }
  .btn:hover {
    background-image: linear-gradient(to left, var(--color-main-light), var(--color-main-dark-2)); }
  .btn:hover .btn__visible {
    transform: translateY(100%); }
  .btn:hover .btn__invisible {
    top: 0; 
  }
  .btn:focus {
    outline: none;
    animation: btn-inline 2s infinite; 
  }

  .btn a{
    text-decoration: none ;
    color:white;
  }