.flex{
    display:flex;
    justify-content:center;

}
.flexItem{
    width: 20rem;
    margin: 32px;
}
@media (max-width: 768px) {
      .flex {
        flex-direction: column;
        align-items: center; 
    }
    .flexItem{
        width: 20rem;
        margin: 10px;
    }
      }