
.flex {
	background-color: #3B3B98;
	color: #fff;
	display: flex;
	font-family: 'Muli', sans-serif;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
}

.flexItem {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #23235B;
	box-shadow: 0px 2px 10px rgba(255, 255, 255, 0.2);
	padding: 20px;
	width: 350px;
	max-width: 100%;
}

@media screen and (max-width: 768px) {
	.flexItem {
    margin: 10rem 0 5rem 0;
	}
}
.input {
    display: flex;
	justify-content: flex-start;
	align-items: center;
	font-size: 18px;
	height: 50px;
    width: 100%;
    outline: none;    
}

label{
    margin: .5rem;
}

.btn{
    margin-top: 1rem;
    border: none;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	padding: 8px 12px;
    background-color: #3B3B98;
    display: block;
	width: 100%;
}


@media screen and (max-width: 768px) {
	.input {
		font-size: 14px;
	}
}